.quiz {
	color: #ffffff;
	user-select: none;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	max-width: 630px;
	margin: 0 auto;
	margin-top: 80px;
	min-height: 320px;

	user-select: none;

	#model-viewer {
		width: 600px;
		height: 400px;
	}

	&__controls {
		display: flex;
	}
	&__modal {
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 1000;

		display: flex;
		justify-content: center;
		align-items: center;

		background: rgba(0, 0, 0, 0.3);	
	}
	&__button {
		line-height: 47px;
		font-weight: 500;
		font-size: 18px;

		height: 47px;

		border-radius: 23.5px;

		&_disabled {
			opacity: 0.2;

			cursor: default;

			&:hover {
				box-shadow: none;
			}
		}

		&_last {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 144px !important;

			color: #F6BD1A;

			.quiz__icon {
				transform: translateY(40%);
			}
		}
	}

	&__title {
		font-family: Open Sans;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 110%;
		/* or 26px */
		text-align: center;

		color: #FFFFFF;
	}

	&__radio {
		display: flex;
		justify-content: space-around;
		margin-top: 96px;
	}

	&__question {
		width: 100%;
	}

	&__radio {
		width: 100%;
	}

	&__info {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 105px;
	}

	&__statusbar {
		width: 100%;
	}

	&__result_model {
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
		@media screen and (max-width: 768px) {
		margin-top: 50px;

		&__title {
			font-size: 1.8rem;
		}
		&__radio {
			margin-top: 30px;
			display: flex;
			flex-wrap: wrap;
		}
		.form__elem {
			max-width: 40%;
			
		}
		.form__label {		
			line-height: 35px;
			font-size: 1.6rem;
			
			.form__image {
				max-width: 75px;
			}

			&[for="2m"] .form__image {
				max-width: 110px;
			}
			&[for="3m"] .form__image {
				max-width: 80px;
			}
		}
		.form__input_large {
			height: 35px;
			padding: 10px;
		}
		.form__input_medium{
			height: 35px;
			padding: 10px;
		}
		.form__input_radio {
			height: 40px;
			width: 40px;
		}
		.form__item {
			width: 100%;
			max-width: 150px;
			height: 200px;

			&_button {
				width: 114px;
				height: 33px;
				line-height: 30px;

				font-size: 1.2rem;
			}
			&_image{
				bottom: 28%;

				& img {
					width: 73px;
				}
			}
		}
		&__button {
			height: 30px;
			font-size: 1.4rem;

			&_last {
				width: 92px !important;
			}
		}
		&__controls {
			margin-left: 20px;
		}
		&__icon {
			width: 20px;
		}
		#quiz__next {
			line-height: 30px;
			width: 92px;
			margin-left: 20px;
		}
		
		#quiz__prev {
			line-height: 26px;
			width: 30px;
		}
		.statusbar__percent {
			font-size: 1.2rem;
		}
		.modal {
			&__body {
				width: 100%;
				height: 100%;
				border-radius: 0;
				padding: 10px;
			}
			&__button {
				transform: scale(0.8);
			}
			#model-viewer {
				width: 100%;
			}
		}
    }}

#quiz__next {
	width: 144px;
	margin-left: 25px;
}

#quiz__prev {
	width: 47px;
}

.form {
	&__image {
		max-height: 386px;
	}
	&__radio {
		display: none;

		&:checked + .form__input {
			border: 2px solid #C7EE86;
		}
	}

	&__label {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		height: 100%;

		cursor: pointer;

		outline: none;

		&:hover .form__input {
			transition: 0.1s;
			border: 2px solid #C7EE86;
			box-shadow: 0px 0px 30px rgba(199, 238, 134, 0.5), inset 0px 0px 15px rgba(199, 238, 134, 0.35) !important;
		}
	}
	&__input {
		font-family: Montserrat;
		font-style: normal;
		font-weight: 500;
		font-size: 2.4rem;
		line-height: 110%;
		/* or 26px */
		text-align: center;

		display: flex;
		align-items: center;
		justify-content: center;
		height: 62px;
		margin-top: 15px;

		box-sizing: border-box;
		border-radius: 41px;
		border: 2px solid rgba(0, 0, 0, 0);
		transition: 0.1s;
		color: #FFFFFF;

		cursor: pointer;

		&:hover {
			border: 2px solid #C7EE86;
			box-shadow: 0px 0px 30px rgba(199, 238, 134, 0.5), inset 0px 0px 15px rgba(199, 238, 134, 0.35) !important;
		}

		&_radio {
			width: 62px;
		}

		&_large {
			padding: 0 45px;
		}

		&_medium {
			padding: 0 19px;
		}
	}

	&__item {
		position: relative;

		width: 31%;
		height: 262px;
		display: flex;
		justify-content: center;
		margin-top: 100px;

		/* Rectangle 6 */

		background: url("../../img/quiz/bg.png") #262626;
		border-radius: 25px;

		cursor: pointer;

		&:hover .form__item_button {
			box-shadow: 0px 0px 50px rgba(246, 189, 26, 0.9), inset 0px -4px 17px rgba(199, 238, 134, 0.9);
			border-bottom: 4px solid #92862d;
		}

		&_button {
			position: absolute;

			bottom: 0;

			display: flex;
			align-items: center;
			justify-content: center;
			height: 62px;
			border-radius: 41px;
			max-width: 140px;
			width: 100%;
			/* ПРИМЕРИТЬ */


			font-family: Montserrat;
			font-style: normal;
			font-weight: 500;
			font-size: 1.4rem;
			line-height: 110%;
			/* or 15px */

			color: #262626;

			transform: translateY(50%);
		}

		&_image {
			position: absolute;
			bottom: 20%;
			left: 50%;

			transform: translateX(-50%);

			img {
				width: 125px;
			}
		}
	}

}

.statusbar {
	&__percent {
		/* Готово: 25% */
		font-family: Montserrat;
		font-style: normal;
		font-weight: normal;
		font-size: 1.4rem;
		line-height: 110%;

		/* or 15px */
		color: #FFFFFF;
	}

	&__bar {
		/* Rectangle 4 */
		position: relative;

		max-width: 328px;
		height: 4px;
		margin-top: 10px;

		background: #262626;
		border-radius: 32px;

		&:after {
			content: "";

			display: block;
			width: 0%;
			height: 100%;

			background: #F6BD1A;
			border-radius: 32px;
		}
	}
}
.modal {
	&__body{
		position: relative;
		padding: 25px 45px 60px 45px;
		z-index: 10001;
		background: #262626;

		border-radius: 25px;

		box-shadow: 0px 0px 50px #262626, inset 0px 0px 50px #262626;
	}
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	&__title {
		font-weight: 500;
		text-align: center;
	}

	&__button {
		position: absolute;
		left: 0;
		top: 0;
		font-size: 1rem;
		height: 40px;
		line-height: 40px;
		border-radius: 40px;
		padding: 0 15px;
		border: none;
		border-bottom: 3px solid #483707;
		box-shadow: none;
		outline: none;

		&:hover {
			box-shadow: none;
			background: #e6ad13;
			border-bottom: 3px solid #443306;
		}
	}
	&__close {
		position: absolute;

		right: 25px;
		top: 30px;

		width: 20px;
		height: 20px;
		cursor: pointer;
		& span {
			position: absolute;
			display: block;
			width: 100%;
			height: 2px;
			background: #ffffff;
			
			&:first-child {
				transform: rotate(45deg);
			}
			&:last-child {
				transform: rotate(-45deg);
			}
		}
	}
}
