.questions {
    position: relative;

    &__body {
        padding-top: 150px;
        padding-bottom: 145px;
    }
    &__title {
        /* Ответьте на несколько вопросов */
        font-family: Open Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 3.6rem;
        line-height: 110%;
        /* identical to box height, or 40px */
        text-align: center;

        color: #FFFFFF;
    }
    &__subtitle {
        /* И посмотрите, как будет выглядить одежда на вас */
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 1.8rem;
        line-height: 110%;
        /* identical to box height, or 20px */
        text-align: center;

        margin-top: 15px;

        color: #FFFFFF;
    }
    &__button {
        position: relative;

        font-style: normal;
        font-weight: 500;
        font-size: 2.1rem;
        line-height: 75px;
        text-align: center;
        text-decoration: none;

        display: block;
        width: 356px;
        margin: auto auto auto auto;

        border-radius: 79px;
        background: #483707;

        transition: 0.1s;

        cursor: pointer;

        &:before {
            /* ПОПРОБУЙТЕ САМИ */
            content: "ПРОЙТИ ТЕСТ";
            top: -4px;
            position: relative;

            display: block;
            width: 356px;
            height: 75px;

            background: #F6BD1A;
            box-shadow: 0px 0px 50px rgba(246, 189, 26, 0.5), inset 0px -4px 17px rgba(199, 238, 134, 0.63);
            border-radius: 79px;

            color: #262626;
        }

        &:hover {
            box-shadow: 0px 0px 50px rgba(246, 189, 26, 0.5), inset 0px -4px 17px rgba(199, 238, 134, 0.63);
        }
    }
    &__area {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 630px;
        margin: 0 auto;
        margin-top: 50px;
        min-height: 320px;

    }
    &__question {
        display: flex;
        justify-content: center;    
    }
    &__background {
        position: absolute;
        top: 0;
        z-index: -1;
    }
    &__underchit {
        left: 0;
    }
    &__skirt {
        right: 0;
        top: 270px;
        
    }

    @media screen and (max-width: 1200px) {
        &__background {
            width: 200px;
        }
        &__underchit {
            top: 240px;
        }
        &__area {
            margin-top: 10px;
        }
    }
    @media screen and (max-width: 992px) {
        &__background {
            width: 160px;
        }
    }
    @media screen and (max-width: 768px) {
        &__body {
            padding-top: 60px;
        }
        &__background {
            display: none;
        }
        &__header {
            max-width: 238px;
            margin: 0 auto;
        }
        &__title {
            font-size: 2.2rem;
            line-height: 110%;
        }
        &__subtitle {
            font-size: 1.4rem;
        line-height: 110%;
        }
        &__area {
            margin-top: 0;
        }
        &__button {
            padding: 0;
            width: 250px;
            height: 50px;
            line-height: 50px;

            &:before {
                font-size: 1.4rem;
                
                padding: 0;
                width: 250px;
                height: 50px;
                line-height: 50px;
            }
		}
    }
}
